"use client";
import React, { useState, useEffect } from "react";
import { ChevronRight } from "lucide-react";

export default function FortinetSignup() {
  const [formData, setFormData] = useState({
    username: "",
    email: "",
    password: "",
    confirmPassword: "",
    agreeToTerms: false,
  });
  const [mounted, setMounted] = useState(false);

  useEffect(() => {
    setMounted(true);
  }, []);

  const handleInputChange = (e) => {
    const { name, value, type, checked } = e.target;
    setFormData((prev) => ({
      ...prev,
      [name]: type === "checkbox" ? checked : value,
    }));
  };

  const handleSubmit = (e) => {
    e.preventDefault();
    console.log("Form submitted:", formData);
    // Handle form submission
  };

  return (
    <div className="min-h-screen bg-black relative overflow-hidden">
      {/* Animated Wave Background */}
      <div className="absolute inset-0 opacity-20">
        <svg
          className="absolute inset-0 w-full h-full"
          viewBox="0 0 1000 1000"
          xmlns="http://www.w3.org/2000/svg"
        >
          <defs>
            <linearGradient
              id="neonGradient"
              x1="0%"
              y1="0%"
              x2="100%"
              y2="100%"
            >
              <stop offset="0%" stopColor="#00ffff" />
              <stop offset="50%" stopColor="#0080ff" />
              <stop offset="100%" stopColor="#00ff80" />
            </linearGradient>
          </defs>
          {/* Multiple flowing lines */}
          <path
            d="M0,100 Q200,50 400,100 T800,100 Q900,120 1000,100 L1000,0 L0,0 Z"
            fill="none"
            stroke="url(#neonGradient)"
            strokeWidth="2"
            className={mounted ? "animate-pulse" : ""}
          />
          <path
            d="M0,200 Q300,150 600,200 T1000,200"
            fill="none"
            stroke="url(#neonGradient)"
            strokeWidth="1"
            opacity="0.6"
            className={mounted ? "animate-pulse" : ""}
            style={{ animationDelay: "1s" }}
          />
          <path
            d="M0,300 Q250,250 500,300 T1000,300"
            fill="none"
            stroke="url(#neonGradient)"
            strokeWidth="1"
            opacity="0.4"
            className={mounted ? "animate-pulse" : ""}
            style={{ animationDelay: "2s" }}
          />
          <path
            d="M0,800 Q400,750 800,800 T1000,800 L1000,1000 L0,1000 Z"
            fill="none"
            stroke="url(#neonGradient)"
            strokeWidth="2"
            className={mounted ? "animate-pulse" : ""}
            style={{ animationDelay: "0.5s" }}
          />
        </svg>
      </div>

      {/* Neon Glow Particles */}
      <div className="absolute inset-0 overflow-hidden pointer-events-none">
        {[...Array(15)].map((_, i) => (
          <div
            key={i}
            className={`absolute w-1 h-1 rounded-full opacity-40 animate-float ${
              i % 3 === 0
                ? "bg-cyan-400 shadow-lg shadow-cyan-400/50"
                : i % 3 === 1
                ? "bg-blue-400 shadow-lg shadow-blue-400/50"
                : "bg-green-400 shadow-lg shadow-green-400/50"
            }`}
            style={{
              left: `${Math.random() * 100}%`,
              top: `${Math.random() * 100}%`,
              animationDelay: `${Math.random() * 8}s`,
              animationDuration: `${4 + Math.random() * 6}s`,
            }}
          />
        ))}
      </div>

      <div className="relative z-10 flex min-h-screen">
        {/* Left Side - Welcome Section */}
        <div className="flex-1 flex flex-col justify-center px-8 lg:px-20">
          {/* Back Button */}
          <button
            className="inline-flex items-center text-gray-400 hover:text-cyan-400 transition-all duration-300 mb-12 group w-fit"
            onClick={() => window.history.back()}
          >
            <span className="mr-2 transform group-hover:-translate-x-2 transition-transform duration-300">
              ‚Üê
            </span>
            Back to Website
            <ChevronRight className="ml-2 w-4 h-4 transform group-hover:translate-x-1 transition-transform duration-300" />
          </button>

          {/* Welcome Text */}
          <div
            className={`transform transition-all duration-1000 ${
              mounted ? "translate-y-0 opacity-100" : "translate-y-10 opacity-0"
            }`}
          >
            <h1 className="text-6xl lg:text-7xl font-bold text-white mb-8 leading-tight">
              Welcome to
              <br />
              <span className="relative">
                <span className="bg-gradient-to-r from-cyan-400 via-blue-500 to-teal-400 bg-clip-text text-transparent">
                  Fortinet.Shopping
                </span>
                <div className="absolute -inset-1 bg-gradient-to-r from-cyan-400/20 via-blue-500/20 to-teal-400/20 blur-lg -z-10"></div>
              </span>
            </h1>
            <p className="text-gray-300 text-xl lg:text-2xl max-w-2xl leading-relaxed font-light">
              Create your account to explore top-tier cybersecurity products,
              enjoy exclusive deals, and manage your orders seamlessly.
            </p>
          </div>
        </div>

        {/* Right Side - Form */}
        <div className="flex-1 flex items-center justify-center px-8 lg:px-16">
          <div
            className={`w-full max-w-lg transform transition-all duration-1000 delay-500 ${
              mounted ? "translate-y-0 opacity-100" : "translate-y-10 opacity-0"
            }`}
          >
            <div className="bg-gray-900/40 backdrop-blur-xl rounded-3xl p-10 border border-gray-700/50 shadow-2xl relative overflow-hidden">
              {/* Neon border effect */}
              <div className="absolute inset-0 bg-gradient-to-r from-cyan-500/10 via-blue-500/10 to-teal-500/10 rounded-3xl blur-sm"></div>

              <div className="relative z-10">
                <div className="text-center mb-10">
                  <h2 className="text-4xl font-bold text-white mb-4">
                    Create an Account
                  </h2>
                  <p className="text-gray-400 text-lg">
                    Already have an Account?{" "}
                    <a
                      href="/login"
                      className="text-cyan-400 hover:text-cyan-300 transition-colors duration-300 font-medium hover:underline"
                    >
                      Log In
                    </a>
                  </p>
                </div>

                <div className="space-y-6">
                  <div className="space-y-5">
                    <div className="relative">
                      <input
                        type="text"
                        name="username"
                        placeholder="Username"
                        value={formData.username}
                        onChange={handleInputChange}
                        className="w-full px-6 py-4 bg-black/50 border border-gray-600/50 rounded-xl text-white placeholder-gray-500 focus:outline-none focus:border-cyan-400 focus:ring-2 focus:ring-cyan-400/20 transition-all duration-300 backdrop-blur-sm"
                        onKeyPress={(e) => e.key === "Enter" && handleSubmit(e)}
                      />
                      <div className="absolute inset-0 rounded-xl bg-gradient-to-r from-cyan-500/0 via-cyan-500/0 to-cyan-500/0 opacity-0 transition-opacity duration-300 hover:opacity-20 pointer-events-none"></div>
                    </div>

                    <div className="relative">
                      <input
                        type="email"
                        name="email"
                        placeholder="Email"
                        value={formData.email}
                        onChange={handleInputChange}
                        className="w-full px-6 py-4 bg-black/50 border border-gray-600/50 rounded-xl text-white placeholder-gray-500 focus:outline-none focus:border-cyan-400 focus:ring-2 focus:ring-cyan-400/20 transition-all duration-300 backdrop-blur-sm"
                        onKeyPress={(e) => e.key === "Enter" && handleSubmit(e)}
                      />
                      <div className="absolute inset-0 rounded-xl bg-gradient-to-r from-cyan-500/0 via-cyan-500/0 to-cyan-500/0 opacity-0 transition-opacity duration-300 hover:opacity-20 pointer-events-none"></div>
                    </div>

                    <div className="relative">
                      <input
                        type="password"
                        name="password"
                        placeholder="Enter Your Password"
                        value={formData.password}
                        onChange={handleInputChange}
                        className="w-full px-6 py-4 bg-black/50 border border-gray-600/50 rounded-xl text-white placeholder-gray-500 focus:outline-none focus:border-cyan-400 focus:ring-2 focus:ring-cyan-400/20 transition-all duration-300 backdrop-blur-sm"
                        onKeyPress={(e) => e.key === "Enter" && handleSubmit(e)}
                      />
                      <div className="absolute inset-0 rounded-xl bg-gradient-to-r from-cyan-500/0 via-cyan-500/0 to-cyan-500/0 opacity-0 transition-opacity duration-300 hover:opacity-20 pointer-events-none"></div>
                    </div>

                    <div className="relative">
                      <input
                        type="password"
                        name="confirmPassword"
                        placeholder="Confirm Password"
                        value={formData.confirmPassword}
                        onChange={handleInputChange}
                        className="w-full px-6 py-4 bg-black/50 border border-gray-600/50 rounded-xl text-white placeholder-gray-500 focus:outline-none focus:border-cyan-400 focus:ring-2 focus:ring-cyan-400/20 transition-all duration-300 backdrop-blur-sm"
                        onKeyPress={(e) => e.key === "Enter" && handleSubmit(e)}
                      />
                      <div className="absolute inset-0 rounded-xl bg-gradient-to-r from-cyan-500/0 via-cyan-500/0 to-cyan-500/0 opacity-0 transition-opacity duration-300 hover:opacity-20 pointer-events-none"></div>
                    </div>
                  </div>

                  <div className="flex items-center space-x-4 py-2">
                    <div className="relative">
                      <input
                        type="checkbox"
                        id="terms"
                        name="agreeToTerms"
                        checked={formData.agreeToTerms}
                        onChange={handleInputChange}
                        className="w-5 h-5 bg-black/50 border-2 border-gray-600/50 rounded focus:ring-cyan-400 focus:ring-2 text-cyan-400 transition-all duration-300"
                      />
                      {formData.agreeToTerms && (
                        <div className="absolute inset-0 bg-cyan-400/20 rounded blur-sm"></div>
                      )}
                    </div>
                    <label
                      htmlFor="terms"
                      className="text-gray-400 text-sm cursor-pointer hover:text-gray-300 transition-colors duration-300"
                      onClick={() =>
                        setFormData((prev) => ({
                          ...prev,
                          agreeToTerms: !prev.agreeToTerms,
                        }))
                      }
                    >
                      I agree to the{" "}
                      <a
                        href="/terms"
                        className="text-cyan-400 hover:text-cyan-300 transition-colors duration-300 underline"
                      >
                        Terms and Conditions
                      </a>
                    </label>
                  </div>

                  <button
                    onClick={handleSubmit}
                    className="w-full bg-gradient-to-r from-gray-800 to-gray-900 hover:from-gray-700 hover:to-gray-800 text-white font-semibold py-4 px-8 rounded-xl transition-all duration-300 transform hover:scale-105 focus:outline-none focus:ring-2 focus:ring-cyan-400 focus:ring-offset-2 focus:ring-offset-black shadow-xl relative overflow-hidden group"
                  >
                    <span className="relative z-10">Create Account</span>
                    <div className="absolute inset-0 bg-gradient-to-r from-cyan-500/0 via-cyan-500/20 to-cyan-500/0 opacity-0 group-hover:opacity-100 transition-opacity duration-300"></div>
                    <div className="absolute inset-0 border border-cyan-400/0 group-hover:border-cyan-400/30 rounded-xl transition-all duration-300"></div>
                  </button>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>

      <style jsx>{`
        @keyframes float {
          0%,
          100% {
            transform: translateY(0px) translateX(0px) rotate(0deg);
            opacity: 0.4;
          }
          25% {
            transform: translateY(-10px) translateX(5px) rotate(90deg);
            opacity: 0.6;
          }
          50% {
            transform: translateY(-20px) translateX(0px) rotate(180deg);
            opacity: 0.8;
          }
          75% {
            transform: translateY(-10px) translateX(-5px) rotate(270deg);
            opacity: 0.6;
          }
        }
        .animate-float {
          animation: float 8s ease-in-out infinite;
        }
      `}</style>
    </div>
  );
}
